*{
	box-sizing: border-box;
}

:root{
	--withe: #fafafa;
	--grey: #282828;
	--black: #1a1a1a;
	--red: #ff4d58;
	--blue: rgba(82, 90, 255);
	--light-blue: #51a2e9;
	--gradient: background-image: radial-gradient(circle at top left, #51a2e9, #ff4d5a);
}

html{
	scroll-behavior: smooth;
}

body{
	margin: 0;
	width: 100vw;
	overflow-x: hidden;
	background-color: var(--black);
}

.App{
	font-family: 'Montserrat', sans-serif;
	color: var(--withe)
}

.canvas-connected{
	position: absolute;
	top: 0;
	left: 0;
	height: 100vh;
	width: 100%;
	z-index: -1;
}

.header{
	z-index: 500;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	height: 100vh;
	font-size: 2rem;
	text-align: center;
	font-weight: 300;
}

.title{
	font-size: 2rem;
	text-align: center;
	font-weight: 300;
	line-height: 1.2em;
	margin-bottom: 1em;
}

.title--text{
	position: relative;
	margin: 0em;
	/* border: white dotted 2px; */
	width: 11em;
}

.animation-right{
	animation-duration: 1.5s;
	animation-name: to-right;
}

@keyframes to-right {
	from {
		right: 2em;
	}
  
	to {
		right: 0;
	}
}

.animation-left{
	animation-duration: 1.5s;
	animation-name: to-left;
}

@keyframes to-left {
	from {
		right: -2em;
	}
  
	to {
		right: 0;
	}
}


.text-red{
	color: var(--red);
}


.view-button{
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.2rem;
	font-weight: 500;
	padding: 0.25em 0.7em;
	color: var(--red);
	text-decoration: none;
	
	border: 2px solid var(--red);
	border-radius: 3px;
	outline: 2px solid var(--red);
	outline-offset: -2px;
	transition: 0.4s;
	backdrop-filter: blur(8px);							/* Esto da el efecto de desenfoque de fondo */
}

.view-button:hover {
	box-shadow: inset 0 0 0.3em var(--red);
	/* border: 1px solid; */
	/* border: 2px solid var(--withe); */
	/* box-shadow: inset 0 0 20px rgba(255, 255, 255, .5), 0 0 20px rgba(255, 255, 255, .2); */
	outline-color: rgba(255, 255, 255, 0);
	outline-offset: 15px;

}

.icon-arrow{
	font-size: 1.2rem;
	margin-left: 0.5em;
}

.animation-top{
	animation-duration: 2.5s;
	animation-name: to-top;
}

@keyframes to-top {
	from {
		top: 11em;
		opacity: 0;
	}

	40% {
		opacity: 0;
		/* top: 10em */
	}
  
	to {
		top: 0;
		opacity: 1;
	}
}


