.menu {
	/* width: 100%;
	height: 10vh; */

	position: sticky;
	top: -1px;
	top: 0px;
	left: 0;
	background-color: rgba(40, 40, 40, 0.9);
	height: 2.5em;
	box-shadow: 0 2px 5px 0px black;
	z-index: 1000;

	backdrop-filter: blur(2px);							/* Esto da el efecto de desenfoque de fondo */

}

.nav-list{
	height: 100%;
	width: 80%;
	margin: 0 auto;
	display: flex;
	justify-content: space-around;
	align-items: center;
	list-style: none;
}

.nav-list li {
	position: relative;
}

.nav-list li::before{
	content: "";
	width: 105%;
	height: 3px;
	background-color: var(--red);
	position: absolute;
	bottom: -19%;
	left: -2%;
	transform: scaleX(0);
	transition: 0.4s ease;
	/* border-radius: 5em; */
}

.nav-list li:hover::before, 
.nav-list li.active::before {
	transform: scaleX(1);
}

.nav-list li a{
	font-size: 0.87rem;
	color: var(--withe);
	text-decoration: none;
}

.menu.active{
	/* background-color: #fff; */
	text-decoration: none;
}

.menu.active .nav-list li a{
	/* color: var(--red); */
}

li.activ.active {
	text-decoration: none;
}

li.active {
	text-decoration: none;
}







